.ImageGridItem, .image-grid-filler {
  flex: 15em;
  margin: 0.5em;
}

.ImageGridItem {
  text-decoration: none;
  color: inherit;
  padding: 0.7em;
  word-wrap: break-word;
  min-width: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 0 0 rgba(0,0,0,0.2);
  transition: all 0.1s;
  &:hover {
    box-shadow: 0.2em 0.2em 0.7em rgba(0,0,0,0.2);
    transform: scale(1.01);
  }
 .image-holder {
    position: relative;
    /* 16:9 aspect */
    padding-top: 56%;
    background: #ddd;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      object-fit: cover;
      transition: opacity 0.5s;
      opacity: 0;
    }
    &.loaded img {
      opacity: 1;
    }
  }
  .item-title {
    font-size: 1.3em;
    margin: 0.4em 0;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .item-description {
    font-size: 0.9em;
    margin: 0.4em 0;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
  .image-caption {
    font-size: 0.7em;
    opacity: 0.7;
    margin: 0.4em -0.5em;
    display: flex;
    justify-content: space-between;
    &>* {
      margin: 0 0.5em;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  button {
    margin-top: .8em;
    padding: 0.5em;
    position: relative;
    background: none;
    border: 1px solid rgba(0,0,0,0.6);
    cursor: pointer;
    opacity: 0.4;
    &:hover, &:focus {
      opacity: .8;
    }
  }
  button.add {
    svg {
      position: absolute;
      left: 0.8em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .item-actions {
    margin: 0 -.3em;
    button {
      border-radius: 100px;
      padding: .7em;
      margin-left: 0.3em;
      margin-right: 0.3em; 
      &:focus {
        outline: none;
        box-shadow: 0 0 0 1px rgb(125, 173, 217);
      }
    }
    svg {
      width: 1.2em;
      height: 1.2em;
      opacity: 0.5;
    }
    .favorite.selected {
      color: red;
      svg {
        opacity: 1;
      }
    }
  }
}