@import '../common';

.Collection {
  .collection-header {
    @include flex-justify;
  }

  h1 {
    margin: 0;
    line-height: 1;
  }

  .new-item-button {
    @include action-button;
  }

  .collection-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1.2em;
  }
}