@mixin flex-justify {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

@mixin action-button {
  text-decoration: none;
  border-radius: 0.2em;
  color: white;
  padding: 0.6em 0.8em;
  padding-left: 2.2em;
  transition: all 0.2s;
  background-color: mediumslateblue;
  box-shadow: 0 0 0 rgba(0,0,0,.1);
  position: relative;
  cursor: pointer;
  border: none;
  display: inline-block;
  svg {
    position: absolute;
    left: 0.8em;
    top: 50%;
    transform: translateY(-50%);
  }
  &:hover:not(:focus) {
    transform: scale(1.03);
    box-shadow: 0.2em 0.2em 0.3em rgba(0,0,0,.2);
  }
}