@import '../common';

.MediaViewer {
  position: fixed;
  overflow-y: auto;
  padding: 2em;
  width: 100%;
  height: 100%;
  top: 0; left: 0;
  background: #333;
  color: #eee;
  .media-header {
    @include flex-justify;
    button {
      border: none;
      padding: .2em;
      background: none;
      opacity: 0.5;
      cursor: pointer;
      color: inherit;
      &:hover {
        opacity: 0.9;
      }
      svg {
        width: 3em;
        height: 3em;
      }
    }
  }
  h1 {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-top: 0;
  }
  video, img {
    width: inherit;
    background: black;
  }
}