@import '../common';

.EditItemForm {
  overflow-y: auto;
  z-index: 1;
  padding: 1.5em;
  &, .form-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .form-overlay {  
    background: rgba(255, 255, 255, 0.95);
  }
  .form-box {
    margin: 1.5em auto;
    position: relative;
    padding: 1em;
    max-width: 30em;
    border-radius: 0.2em;
    background: white;
    box-shadow: 0.5em 0.5em 1.5em rgba(0,0,0,0.2);
  }
  .form-header {
    @include flex-justify;
    h3 {
      margin: 0.2em 0;
    }
    button {
      border: none;
      padding: .2em;
      background: none;
      opacity: 0.3;
      cursor: pointer;
      &:hover {
        opacity: 0.7;
      }
      svg {
        width: 2em;
        height: 2em;
      }
    }
  }
  label {
    position: absolute;
    top: 0.6rem;
    left: 0.6rem;
    font-size: 0.8em;
    opacity: 0.7;
  }
  form>div {
    position: relative;
  }
  input, select, textarea {
    width: 100%;
    font: inherit;
    color: inherit;
    font-size: 1.1em;
    padding: 0.5rem;
    padding-top: 1.8rem;
    margin-bottom: 1rem;
    border: 1px solid rgba(0,0,0,0.2);
  }
  textarea {
    resize: vertical;
  }
  button[type="submit"] {
    @include action-button;
  }
}